.container {
  width: auto;
  height: 629px;
  background-image: linear-gradient(360deg,
      rgba(0, 0, 0, 0.8) 0%,
      rgba(0, 0, 0, 0) 15%),
    linear-gradient(180deg, rgba(0, 0, 0, 0.8) 0%, rgba(0, 0, 0, 0) 15%),
    url(../../assets/images/services-bg.png);
  background-repeat: no-repeat;
  background-size: cover;
  padding: 120px 64px;
  font-family: Geologica;
  line-height: 140%;
}

.slider_container {
  display: flex;
  flex-direction: row;
  gap: 24px;
}

.custom_splide {
  display: flex;
  align-items: center;
  position: relative;
  z-index: 0;
  margin-right: -64px;
  margin-left: -64px;
}

.custom_splide button {
  filter: invert(90%) sepia(31%) saturate(2%) hue-rotate(194deg) brightness(107%) contrast(100%);
  opacity: 0.5;
  background-color: transparent;
  height: 100%;
  width: 100px;
  transition-duration: 0.3s;
}

.custom_splide::before {
  content: "";
  display: block;
  position: absolute;
  z-index: 1;
  left: 0;
  width: 100px;
  height: 100%;
  background: linear-gradient(90deg, #261c1a 0%, rgba(38, 28, 26, 0) 100%);
  cursor: pointer;
}

.custom_splide::after {
  content: "";
  display: block;
  position: absolute;
  right: 0;
  width: 128px;
  height: 100%;
  background: linear-gradient(270deg, #261c1a 0%, rgba(38, 28, 26, 0) 100%);
  cursor: pointer;
}

a {
  text-decoration: none;
}

.button:hover {
  cursor: pointer;
  background: rgba(255, 255, 255, 0.5);
}

.splide__arrow {
  background: transparent;
}

@media screen and (max-width: 905px) {
  .container {
    padding: 80px 40px;
    height: 717px;
    position: relative;
  }

  .services__header {
    display: flex;
    margin-bottom: 20px;
    align-items: flex-start;
    gap: 8px;

    ul {
      padding-right: 0;
    }

    a button {
      position: absolute;
      bottom: 80px;
      left: 40px;
      width: 90%;
    }
  }

  .custom_splide {
    margin-right: -40px;
    margin-left: -40px;
  }

  .custom_splide::before,
  .custom_splide::after {
    display: none;
  }
}

